<template>
  <div id="radioBar">
    <el-menu class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" router>
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-erp-Piechart"></i>
          <span slot="title">v-chart</span>
        </template>
        <el-menu-item v-for="n in 15" :key="n" :index="vCharts.num[n-1]">{{ vCharts.title[n-1] }}</el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">导航一</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="2-1">选项1</el-menu-item>
          <el-menu-item index="2-2">选项2</el-menu-item>
        </el-menu-item-group>
        <el-menu-item index="2-3">选项3</el-menu-item>
        <el-submenu index="2-4">
          <span slot="title">选项4</span>
          <el-menu-item index="2-4-1">选项1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="3">
        <i class="el-icon-menu"></i>
        <span slot="title">导航二</span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-document"></i>
        <span slot="title">导航三</span>
      </el-menu-item>
      <el-menu-item index="5">
        <i class="el-icon-setting"></i>
        <span slot="title">导航四</span>
      </el-menu-item>
    </el-menu>

  </div>
</template>

<script>
  export default {
    name: 'radioBar',
    props: ['isCollapse'],
    data() {
      return {
        vCharts: {
          num: [
            "/index/vcharts",
            "/index/vline",
            "/index/vhistogram",
            "/index/vbar",
            "/index/vpie",
            "/index/vring",
            "/index/vwaterfull",
            "/index/vfunnel",
            "/index/vradar",
            "/index/vmap",
            "/index/vsankey",
            "/index/vheatmap",
            "/index/vscatter",
            "/index/vcandle",
            "/index/vgauge",
            "/index/vtree"
          ],
          title: [
            "vCharts",
            "折线图",
            "柱状图",
            "条形图",
            "饼图",
            "环图",
            "瀑布图",
            "漏斗图",
            "雷达图",
            "地图",
            "桑基图",
            "热力图",
            "散点图",
            "K图",
            "树图",
            "仪表盘"
          ]
        }
      };
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }

</script>

<style scoped>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;

  }

  .el-menu {
    height: calc(100vh - 60px);
    background: #EBF1F6;

  }

  ul.el-menu--popup li.el-menu-item {
    line-height: 40px;
    height: 40px;
    border-bottom: 1px solid #f4f4f4;
  }

  ul.el-menu--popup {
    padding: 0 !important;
  }

</style>



